import * as React from 'react';

import './Logo.css';

const Logo = () => (
  <div className='logo'>
    <a href='/'>
      <img className='logo__img' src='https://avatars2.githubusercontent.com/u/11474330' alt='logo'/>
    </a>
  </div>
);

export default Logo;



